<!--子组件-->
<template>
  <div class="tapBar-item">
    <slot name="leftIcon"></slot>
    <p v-text="iconText"></p>
    <slot name="rightIcon"></slot>
  </div>
</template>
<script>
  export default{
    props:['iconText']
  }
</script>
<style>
  .tapBar-item{
    display: flex;
    justify-content:space-between;
  }
  .tapBar-item p{
    text-align: center ;
    font-size:0.825rem;
    color: #fff;
    margin-top:0.7rem;

  }
  .tapBar-item img{
    margin-top:0.2rem;
    transform:scale(.7);
  }
</style>



