<!--父组件-->
<template>
  <div class="box">
    <div class="topbar">
      <item iconText="平驾租车">
        <img src="./../assets/icon/people.png" alt="" slot="leftIcon" @click="up">
        <img src="./../assets/icon/message.png" alt="" slot="rightIcon">
      </item>
    </div>
  </div>

</template>
<script>
  import item from './topbar-item.vue'
  export default{
      components:{
        item
      },
    methods:{
      up(){
        this.$emit('upup'); //主动触发upup方法，'hehe'为向父组件传递的数据
      }
    }
  }
</script>
<style>
  *{
    margin:0;
    padding:0;
  }
  .box{
    position:fixed;
    top:0;
    left:0;
    width: 100%;
    background-color: #054c92;
    height:2.325rem;
    z-index:99;
  }
  .topbar{
    width: 90%;
    height:2.325rem;
    margin:0 auto;
    background-color: #054c92;

  }

</style>



