<template>
  <div>
      <topBar @upup="change"></topBar>
    <div id="allmap">

    </div>
    <p class="text">全部20款车型</p>
    <div class="product">
      <div class="section-item">
        <div class="section-item-left">
          <img src="../../assets/car/宝马.png" alt="">
        </div>
        <div class="section-item-right">
          <p>宝马</p>
          <p>日租金 <span>￥888</span> </p>
          <p>周租金<span>￥5888</span></p>
        </div>
      </div>
      <div class="section-item">
        <div class="section-item-left">
          <img src="../../assets/car/奔驰.png" alt="">
        </div>
        <div class="section-item-right">
          <p @click="fn">梅德赛斯 奔驰</p>
          <p>日租金 <span>￥888</span> </p>
          <p>周租金<span>￥5888</span></p>
        </div>
      </div>
      <div class="section-item">
        <div class="section-item-left">
          <img src="../../assets/car/丰田.png" alt="">
        </div>
        <div class="section-item-right">
          <p>丰田汉兰达</p>
          <p>日租金 <span>￥888</span> </p>
          <p>周租金<span>￥5888</span></p>
        </div>
      </div>
      <div class="section-item">
        <div class="section-item-left">
          <img src="../../assets/car/东风-1.png" alt="">
        </div>
        <div class="section-item-right">
          <p>东风-标致508</p>
          <p>日租金 <span>￥888</span> </p>
          <p>周租金<span>￥5888</span></p>
        </div>
      </div>
    </div>

    <people v-show="showcome"></people>
    <tapBar></tapBar>
  </div>
</template>
<script>
  import topBar from './../top-bar.vue'
  import tapBar from './../tap-bar.vue'
  import people from './people-center.vue'
  export default{
    data(){
      return{
        showcome:false
      }
    },
    components:{
      topBar,
      tapBar,
      people
    },
    mounted () {
      this.initMap()
    },
    methods: {
      change(){
        this.showcome = !this.showcome
      },
      fn:function(){
//    个人中心点击显示隐藏
      this.showcome=!this.showcome;
      },
      //这几个地方加this
      initMap () {
        this.createMap() ; //创建地图
        this.setMapEvent();//设置地图事件
        this.addMapControl();//向地图添加控件
        this.addMarker();//向地图中添加marker
      },
      createMap(){
        let map = new BMap.Map("allmap");//在百度地图容器中创建一个地图
        let point = new BMap.Point(113.64964385,34.75661006);//定义一个中心点坐标
        map.centerAndZoom(point,12);//设定地图的中心点和坐标并将地图显示在地图容器中
        window.map = map;//将map变量存储在全局
      },
      setMapEvent(){
        map.enableDragging();//启用地图拖拽事件，默认启用(可不写)
        map.enableScrollWheelZoom();//启用地图滚轮放大缩小
        map.enableDoubleClickZoom();//启用鼠标双击放大，默认启用(可不写)
        map.enableKeyboard();//启用键盘上下左右键移动地图
      },
      addMapControl(){
        //向地图中添加缩放控件
        var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
        map.addControl(ctrl_nav);
        //向地图中添加缩略图控件
        var ctrl_ove = new BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
        map.addControl(ctrl_ove);
        //向地图中添加比例尺控件
        var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
        map.addControl(ctrl_sca);
      },
      //标注点数组
      //创建marker
      addMarker(){
        var markerArr = [{title:"无锡云熵动力科技有限公司",content:"地址：无锡市惠山经济开发区智慧路18号<br/>电话：15061509527",point:"120.32801|31.686872",isOpen:1,icon:{w:23,h:25,l:46,t:21,x:9,lb:12}}];
        for(var i=0;i<markerArr.length;i++){
          var json = markerArr[i];
          var p0 = json.point.split("|")[0];
          var p1 = json.point.split("|")[1];
          var point = new BMap.Point(p0,p1);
          //这个地方加this
          var iconImg = this.createIcon(json.icon);
          var marker = new BMap.Marker(point,{icon:iconImg});
          //这个地方加this
          var iw = this.createInfoWindow(i);
          var label = new BMap.Label(json.title,{"offset":new BMap.Size(json.icon.lb-json.icon.x+10,-20)});
          marker.setLabel(label);
          map.addOverlay(marker);
          label.setStyle({
            borderColor:"#808080",
            color:"#333",
            cursor:"pointer"
          });
          var index = i;
          //这个地方加this
          var _iw = this.createInfoWindow(i);
          var _marker = marker;
          marker.addEventListener("click",function(){
            //这个地方加this
            this.openInfoWindow(_iw);
          });
          iw.addEventListener("open",function(){
            _marker.getLabel().hide();
          })
          _iw.addEventListener("close",function(){
            _marker.getLabel().show();
          })
          label.addEventListener("click",function(){
            _marker.openInfoWindow(_iw);
          })
          if(!!json.isOpen){
            label.hide();
            _marker.openInfoWindow(_iw);
          }
        }
      },
      //创建InfoWindow
      createInfoWindow(i){
        //这个地方复制一下上面的var markerArr 不然会不显示报错
        var markerArr = [{title:"无锡云熵动力科技有限公司",content:"地址：无锡市惠山经济开发区智慧路18号<br/>电话：15061509527",point:"120.32801|31.686872",isOpen:1,icon:{w:23,h:25,l:46,t:21,x:9,lb:12}}];
        var json = markerArr[i];
        var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>"+json.content+"</div>");
        return iw;
      }
      }
  }
</script>
<style>
  #allmap{
      width: 100%;
      height: 20.25rem;
      margin-top:1.5rem;
    }
  .product{
    width: 17.75rem;
    height: 7.85rem;
    margin:0 auto;
    margin-left:0.55rem;
    display: flex;
    justify-content:space-between;
    flex-wrap:wrap;
  }
  .text{
    padding:0.5rem;
    font-size:0.6rem;
    color: #b9b9b9;
  }
  .section-item{
    width: 50%;
    height: 3.5rem;
    display:flex;
    justify-content:space-between;
  }
  .section-item-left{
    width:50%;
    height: 3.5rem;
    background-color: #f9f9f9;
  }
  .section-item-right{
    width:50%;
    height: 3.5rem;
  }
  .section-item-left>img{
    width: 100%;
    padding:0.85rem 0;
  }
  .section-item-right>p:nth-of-type(1){
    font-size:0.625rem;
    color: #606060;
    margin-left:0.45rem;
  }
  .section-item-right span{
    color: #f91f1f;
  }
  .section-item-right>p:nth-of-type(2){
    font-size:0.55rem;
    padding:0.4rem 0;
    color: #606060;
    margin-left:0.45rem;
  }
  .section-item-right>p:nth-of-type(3){
    font-size:0.55rem;
    color: #606060;
    margin-left:0.45rem;
  }
  .people-center{
    width: 14.65rem;
    height: 42.4rem;
    position:absolute;
    top:0;
    left:-14.65rem;
    background-color: #919191;
    z-index:999;
  }
  .people-center-show{
    width: 14.65rem;
    height: 42.4rem;
    position:absolute;
    top:0;
    left:0;
    background-color: #919191;
    z-index:999;
  }
</style>



